*{
  box-sizing: border-box;
}
body {
  font-family: Arial, Helvetica, sans-serif;
}

/* Float four columns side by side */
.card {
  width: 18rem;
  float: left;
  margin: 40px;
}

/* Clear floats after the columns */
.card-container:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
  .card {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

nav{
  display: flex;
  justify-content: space-between;
}
.container-fluid button{
  width: 80px;
  margin-right: 50px;
}

.card-title{
  margin-bottom: 25px;
}
.card-button{
  text-align: center;
  margin-top: 30px;
}
.card-button button{
  margin: 10px;
  width: 80px;
}
